.popup__input {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  display: block;
  padding-bottom: 10px;
  width: 100%;
}

.popup__input:focus {
  outline: none;
 }